/*
    photo style
 */

.photo {

  @include mq(md) {
    padding: 37px 0 57px;
  }

  &__content {

    &:before {
      width: 76px;
      height: 73px;
      background: url("../img/gallery/2.png") no-repeat center/cover;
      top: -53px;
      left: -38px;
      z-index: 1;
      pointer-events: none;

      @include mq(xxl) {
        left: 0;
      }

      @include mq(md) {
        width: 60px;
        height: 57px;
        top: -57px;
        left: calc(50% - 8px);
        transform: translateX(-50%);
      }
    }

    &:after {
      width: 228px;
      height: 236px;
      background: url("../img/gallery/3.png") no-repeat center/cover;
      bottom: -83px;
      right: -151px;

      @include mq(xl) {
        bottom: -153px;
        right: -51px;
      }

      @include mq(md) {
        width: 78px;
        height: 81px;
        bottom: -67px;
        left: -10px;
      }
    }
  }
}